body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.pagination {
  margin: auto;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.taskBoardTitle {
  margin: auto;
  text-align: center; 
}

 .mainForm {
  margin: auto;
  width:1024px;
}

.userInfo {
  margin:auto;
  display:-webkit-flex;
  display:flex;
  width: 30%;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}

.signInRefer {
  color:blue;
  cursor:pointer;
}

.task {
    position: relative;
    width: 300px;
    height: 130px;
    border: 1px solid black;
    border-radius: 10px;
    box-shadow: -4px -4px 36px -4px rgba(0,0,0,0.75);
    margin:  0.5rem auto;
}

.imgWrapper {
    padding: 3px;
    position: absolute;
    right:0;
}
.imgWrapper>img {
    height: 24px;
    width: 24px;
}
.content {
    padding: 0.5rem 0 0 1rem;
    font-weight: 700;
    line-height: 0.8;
}


.btn {
    width: 150px;
    height: 30px;
    background-color: grey;
    margin: 30px auto;
    display: block;
    font-size: 16px;
    font-weight: 700;
    text-align: center;
    border: 1px solid black;
    border-radius: 10px;
    box-shadow: -4px -4px 36px -4px rgba(0,0,0,0.75);

}
.owerlay {
    position: fixed;
    width:100%;
    height:100%;
    background-color:rgba(70, 73, 73, 0.5);
    z-index: 12;
}

.formWrapper {
    position: relative;
    z-index: 15;
    border: 1px solid black;
    border-radius: 12px;
    background-color: rgb(249, 250, 250);
    position: absolute;
    top:50%;
    left:50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 35%;
}

.formWrapper>img {
position: absolute;
right: 5px;
top:5px;
}

.modalForm{
    width: 70%;
    margin: auto;
    padding-top:1.8rem;
}

.contentWrapper {
    margin-top:0.5rem;
    width:30%; 
}

.inputContent {
    width: 100%;
    height: 2rem;
}
.sortStyle {
    margin: 0.5rem auto;
    text-align: center;
}
